<template>
    <div class="exchange">
        <searchExchange></searchExchange>
        <div class="title-group">
            <p class="title">交換列表</p>
                <div class="sort-type">排序方式 
                <span @click="toggleSortType('date')" :class="{'active':searchForm.sortType == 'date'}">最新</span>|
                <span @click="toggleSortType('hot')" :class="{'active':searchForm.sortType == 'hot'}">熱門</span>
            </div>
        </div>
        <exchangeItem :itemList = "allItems" @success="success"></exchangeItem>
        <el-pagination
            background
            layout="prev, pager, next"
            :page-size="12"
            :total="pageSetting.totalPages"
            :current-page.sync ="pageSetting.curPage"
            @current-change="getExchangeItemList()"
            >
        </el-pagination>
    </div>
</template>

<script>
import searchExchange from './components/searchExchange.vue'
import exchangeItem from './components/exchangeItem.vue'
import { getExchangeItemList  } from '../../api/exchange'; 

export default {
    name: 'Exchange',
    components:{
        searchExchange,
        exchangeItem,
    },
    data(){
        return {
            searchForm:{
                sortType:'date',
                creator:'',
                category:"",
                ownMember: "",
                targetMember: ""
            },
            allItems: [],
            pageSetting:{
                totalPages: null,
                curPage: 1,
            }
            
        }
    },
    mounted(){
        this.getExchangeItemList()
    },
    methods:{
        toggleSortType(type){
             this.searchForm.sortType = type
             this.pageSetting.curPage = 1
             this.getExchangeItemList()
        },
        getExchangeItemList(){
            let params = this.searchForm
            params.curPage = this.pageSetting.curPage
            getExchangeItemList(params).then(res => {
                this.allItems = res.body
                this.pageSetting.totalPages = res.totalCount
                }).catch(err => {
                    console.log(err);
                });
        },
        success(){
             this.getExchangeItemList()
        }
    }
}
</script>

<style lang="scss" scoped>
.exchange {
    .title-group {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 50px auto 0;
        max-width: 1200px;
        box-sizing: border-box;
        padding: 0 27px;
    }
    .title {
        font-size: 30px;
    }
    .sort-type{
        span {
            margin: 0 10px;
            color: $textColor;
            &:hover,&.active {
                color:$mainColor;
            }
        }
    }
    .el-pagination {
        text-align: center;
    }
}

</style>